.main-container-dark{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #1F1F1F;
  color: #fff;
}
.main-container-light{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #f5ddd3;
  color: rgb(0, 0, 0);
}

.content-container{
	width: calc(100% - 70px);
    position: fixed;
    height: 100%;
    margin-left: 70px;
    overflow-y: auto;
}

.content {
    margin-right: 5%;
    margin-left: 5%;
    margin-top: 5vh;
    height: calc(100% - 5vh);
}

.container{
width: 100vw;
height: 100vh;
position: fixed;
right: 0px;
}

.menu-icon{
height: 35px;
position: fixed;
top: 10px;
left: 10px;
padding: 5px 0;
background-color: #0f0f0f;
}

.menu-icon:hover{
background-color: #161616;
}

.menu-icon:active{
background-color: rgb(32, 32, 32);
fill: #e3e3e3;
}

.nav-container-dark{
  position: fixed;
  top: 0;
  width: 70px;
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #0f0f0f;
  left: 0;
  transition: width 0.2s ease-in-out;
  overflow: hidden;
  z-index: 2;
}
.nav-container-light{
  position: fixed;
  top: 0;
  width: 70px;
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #bda6a6;
  left: 0;
  transition: width 0.2s ease-in-out;
  overflow: hidden;
  z-index: 2;
}

.nav-container-dark:hover{
  width: 200px;
}
.nav-container-light:hover{
  width: 200px;
}
.navBar-imageOptions{
	width: 35px;
	margin-left: 15px;
}


.nav-logo{
  width: 35px;

}

.nav-option{
font-family: 'Roboto', sans-serif;
text-decoration: none;
display: inline-flex;
color: #fff;
width: 100%;
font-size: 20px;
height: 60px;
align-items: center;
}

.nav-option:hover{
background-color: #3a3a3a;
}

.nav-option-container{
display: inline-flex;
border-bottom-left-radius: 50px;
border-top-left-radius: 50px;
align-items: center;
height: 60px;
padding-left: 10%;
}

.navBar-optionName{
margin-left: 1.3rem;
white-space: nowrap;
}

.nav-option-container:first-of-type{
margin-top: 70%;
}

.nav-option-container:hover{
background-color: #0f0f0f;
}

.nav-options{
  height: 30%;
}

.logOut-navBar{
  position: absolute;
  bottom: 0;
  margin-bottom: 20px;
  cursor: pointer;
}

